import Axios from "axios";
import Config from "../../config";

const defaultState = {
  practices: []
};
const getters = {
  PRACTICE: state => {
    return state.practices;
  }
};
const mutations = {
  SET_PRACTICE: (state, payload) => {
    state.practices = payload;
  },
  UPDATE_PRACTICE: (state, payload) => {
    console.log("payload - ", payload);
  }
};
const actions = {
  FETCH_PRACTICE: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/practice");
    context.commit("SET_PRACTICE", data);
  },
  UPDATE_PRACTICE: async ({ commit }, payload) => {
    const filteredPayload = {
      npi: payload.npi,
      submittingBills: payload.submittingBills
    };
    //    console.log("filteredPayload - ", filteredPayload);
    // const res = await Axios.post("http://localhost:3000/physicians", filteredPayload);
    // if (!res.success) {
    //   return false;
    // }
    commit("UPDATE_PRACTICE", filteredPayload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
