import Axios from "axios";
import Config from "../../config";

const defaultState = {
  entity: []
};
const getters = {
  ENTITY: state => {
    return state.entity;
  }
};
const mutations = {
  SET_ENTITY: (state, payload) => {
    state.entity = payload;
  },
  UPDATE_ENTITY: (state, payload) => {
    console.log("payload - ", payload);
  }
};
const actions = {
  FETCH_ENTITY: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/entity");
    context.commit("SET_ENTITY", data);
  },
  UPDATE_ENTITY: async ({ commit }, payload) => {
    // only set the fields that are "editable"
    //   console.log("payload - ", payload);

    const filteredPayload = {
      npi: payload.npi,
      submittingBills: payload.submittingBills
    };
    //    console.log("filteredPayload - ", filteredPayload);
    // const res = await Axios.post("http://localhost:3000/physicians", filteredPayload);
    // if (!res.success) {
    //   return false;
    // }
    commit("UPDATE_ENTITY", filteredPayload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
