import Axios from "axios";

const defaultState = {
  audits: [{ auditId: 1, patientName: "patient 1", unitsAudited: 5 }, { auditId: 2, patientName: "patient 2", unitsAudited: 7 }, { auditId: 3, patientName: "patient 3", unitsAudited: 6 }]
};
const getters = {
  AUDITS: state => {
    return state.audits;
  }
};
const mutations = {
  SET_AUDITS: (state, payload) => {
    state.audits = payload;
  },
  UPDATE_AUDITS: (state, payload) => {
    state.audits = payload;
  }
};
const actions = {
  FETCH_AUDITS: async context => {
    let { data } = await Axios.get(Config.apiRoot + "/physicians");
    context.commit("SET_AUDITS", data);
  },
  UPDATE_AUDITS: async ({ commit }, payload) => {
    // only set the fields that are "editable"
    const filteredPayload = {
      auditId: payload.auditId,
      unitsAudited: payload.unitsAudited
    };
    // const res = await Axios.post("http://localhost:3000/physicians", filteredPayload);
    // if (!res.success) {
    //   return false;
    // }
    commit("UPDATE_AUDITS", filteredPayload);
    return true;
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
